import WorksData from './data/works.json';

class Sidebar extends Component {
  state = {
    isHovering: false,
  }

  handleMouseHover = () => {
    this.setState(this.toggleMouseState)
  }

  toggleMouseState = () => {
    return {
      isHovering: !this.state.isHovering
    };
  }

  render() {
    return (
      <Row>
        <Col lg={12}>
          <div className="btm-nav">
            <ul className="contact">
              <a
                href="mailto:junhaotom@gmail.com"
                onMouseEnter={this.handleMouseHover}
                onMouseOut={this.handleMouseHover}
              >
                {(!this.state.isHovering && <li>email</li>) || (
                  <li>junhaotom@gmail.com</li>
                )}
              </a>
              <a href="https://www.behance.net/realtomkwok">
                <li>behance</li>
              </a>
              <a href="https://www.instagram.com/realtomkwok/">
                <li>instagram</li>
              </a>
              <a href="">
                <li>résumé</li>
              </a>
            </ul>
            <div className="credit">
              <span className="chinese-name">郭 君昊</span>
              <br></br>
              <span>© Tom Kwok 2020</span>
            </div>
          </div>
        </Col>
      </Row>
    );
  }
}

