class Works extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isHovering: false
    }
  }
  
  handleMouseHover = () => {
    this.setState(this.toggleMouseState);
  }

  toggleMouseState = () => {
    return {
      isHovering: !this.state.isHovering,
    };
  }

  render() {
    return (
      <Row
        className="works"
        onMouseEnter={this.handleMouseHover}
        onMouseLeave={this.handleMouseHover}
      >
        <Col className="works-name" lg={12}>
          <a href={this.props.url}>{this.props.name}</a>
        </Col>
        {this.state.isHovering && (
          <div className="works-img">
            <img src={this.props.img}></img>
          </div>
        )}
      </Row>
    );
  }
}

function App() {
  return (
    <div>
      <div className="top-divider" />
      <Grid fluid>
        <Row className="header" between="lg" between="md">
          <Col lg={3} md={4}>
            <h1>
              <a href="/">
                <span className='eng-name'>Tom Kwok</span>
                <br></br>is a designer focused on visual design.
              </a>
            </h1>
          </Col>
          <Col lg={1} md={2}>
            <ul className="nav nav-2">
              <a href="#">
                <li>about</li>
              </a>
              <a href="#">
                <li>works</li>
              </a>
            </ul>
          </Col>
        </Row>
        <Row top="lg">
          <Col
            className="sidebar"
            lg={3}
            md={4}
            last="xs"
            first="lg"
            first="md"
          >
            <Sidebar />
          </Col>
          <Col className="content" lg={9} md={8}>
            {WorksData.map((worksDetail, index) => {
              return (
                <Works
                  name={worksDetail.title}
                  img={worksDetail.hero_src}
                  url={worksDetail.url}
                ></Works>
              );
            })}
          </Col>
        </Row>
      </Grid>
    </div>
  );
}

export default App;
